import Vue from 'vue'
import {version,TICKET_STORAGE,serverPath} from '../public-variable'
import Md5 from 'js-md5'
import {getStorage,setStorage,removeStorage} from './storage'
import VueResource from 'vue-resource';
import store from '../../store'
Vue.use(VueResource);
Vue.http.options.emulateJSON = true;



/*获取ticket，存本地缓存，每次获取ticket判断缓存中是否过期，如果过期重新拉取，未过期直接获取*/
export function getTicket() {
  let ticket = getStorage(TICKET_STORAGE);
  if (!ticket) {
    const device = store.state.device.currentDevice;
    console.log('服务器重新拉取ticket')
    let paras = {
      secret: device.secret,
      version: version,
      type: device.deviceType,
      sign: Md5(device.secret + version + device.deviceType)
    }
    Vue.http.post(serverPath + 'getTicket', paras).then(response => {
      if (response.status === 200) {
        let data = response.body;
        console.log('拉取结果。。。。。')
        console.log(data)
        if (data.code == 0) {
          let today = new Date();
          today.setHours(23);
          today.setMinutes(59)
          today.setSeconds(40);
          let ticketVal = {
            ticket: data.data,
            timeout: today
          };
          setStorage(TICKET_STORAGE, ticketVal);
        }
      } else {
        return null;
      }
    }, response => {
      // error callback
      console.log('服务器拉取ticket出错')
    });
  } else {
    if (new Date(ticket.timeout) > new Date()) {
      console.log('获取缓存中的tiket:' + ticket.ticket)
      return ticket.ticket;
    }
    else {
      console.log('缓存中ticket过期，重新拉取服务器')
      removeStorage(TICKET_STORAGE);
      getTicket();
    }
  }
}
const ERR_OK=0;

//secret+version+type
/*url--服务器接口方法名
* params-参数数组，严格按照接口参数顺序，否则验证过不去
* callback-回调函数
* */
export  function post(url,params,callback) {
  try {
    let ticket = getTicket();
    let para = {
      version: version,
      ticket: ticket
    };
    if (params) {
      for (var p in params) {
        para[p] = params[p]
      }
    }
    let signString = '';
    for (var p in para) {
      signString += para[p];
    }
    para['sign'] = Md5(signString);//生成sign
    Vue.http.post(serverPath + url, para).then((response) => {
      if (response.status === 200) {
        let data = response.body;
        console.log('------------请求结果----------------')
        console.log(data.data)
        if (data) {
          if (data.code == 0) {
            callback(data.data, true)
          }
          else {
            callback(data.msg, false);
          }
        }
      }
    }, response => {
      callback(response.statusText, false)
    });
  }
  catch (e) {
    throw e;
  }
}






