export const cartStatistics=state=>{
  var totalMoney=0;
  var totalCount=0;
  var sellers=[];
  if(state.cart.cartInfo) {
    state.cart.cartInfo.forEach(function (cartItem, item) {
      if(cartItem.checked) {
        totalMoney += cartItem.price * cartItem.quantity;
        totalCount += cartItem.quantity;
      }
      sellers.push(cartItem.sellerId)
    })
  }
  return {
    totalMoney:totalMoney,
    count:totalCount,
    sellers:Array.from(new Set(sellers))
  };
};



