import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

Vue.use(ConfirmPlugin);
require('es6-promise').polyfill()

if (window.location.protocol === 'file:' || window.location.port === '3000') {
  var cordovaScript = document.createElement('script')
  cordovaScript.setAttribute('type', 'text/javascript')
  cordovaScript.setAttribute('src', 'cordova.js')
  document.body.appendChild(cordovaScript)
}
let device=detectOS();
console.log('当前访问设备：'+device);
store.commit('CURRENT_ACCESS_DEVICE',{device:device});

function detectOS() {
  var sUserAgent = window.navigator.userAgent;

  if(sUserAgent.toLowerCase().match(/MicroMessenger/i) == 'micromessenger') {
    return "chat";
  }
  var isWin = (navigator.platform == "Win32") || (navigator.platform == "Windows");
  var isMac = (navigator.platform == "Mac68K") || (navigator.platform == "MacPPC") || (navigator.platform == "Macintosh") || (navigator.platform == "MacIntel");
  var isUnix = (navigator.platform == "X11") && !isWin && !isMac;
  if(isWin||isMac||isUnix){
    return "wap";
  }
  var isLinux = (String(navigator.platform).indexOf("Linux") > -1);
  var bIsAndroid = sUserAgent.toLowerCase().match(/android/i) == "android";
  var bIsIos = sUserAgent.toLowerCase().match(/ios/i) == "ios";
  if (isLinux) {
    if (bIsAndroid) return "android";
    else return "wap";
  }
  if(bIsIos){
    return "ios";
  }
  if (isWin) {
    return "wap";
  }

  return "other";
}

/* eslint-disable no-new */
new Vue({
  el: 'app',
  router,
  store,
  // components: { App }
  render: h => h(App)
})




