<template>
<div>
  <v-productbanner :imgList="product.images"></v-productbanner>
  <div>{{product.productName}}</div>
  <div>￥{{product.price}}</div>
  <input type="button" value="加入购物车" @click="addToCart()">
</div>
</template>

<script type="text/ecmascript-6">
  import vProductbanner from '../../components/product/product-banner.vue'

  const ERR_OK=0;
export  default{
  data(){
    return {
      sku: '',
      product: {}
    }
  },
  created(){

    this.sku = this.$route.params.sku;
    this.$http.get('/api/products').then((response) => {
      response = response.body;
      if (response.errno === ERR_OK) {
        response.data.forEach((pro) => {
          if (pro.sku === this.sku) {
            this.product = pro;
          }
        });
      }
    });
  },
  components: {
    vProductbanner
  },
  methods:{
      addToCart(){
        this.$store.dispatch('addToCart',this.product);
          //this.$router.go({name:'cart'});
      }
  }
}
</script>

<style>

</style>



