/**
 * Created by shen on 2017/3/1.
 * 验证用户是否登录，如果已登录自动重新登录（token已过期情况下），如果在其他设备修改过密码，对用户进行提示，重新登录
 */
import {USERINFO_STORAGE,TIP_MESSAGE} from '../public-variable'
import {getStorage,setStorage,removeStorage} from './storage'
import {post} from './public-resource'
export function isLogin() {
  let userInfo = getStorage(USERINFO_STORAGE);
  if (userInfo) {
    if (userInfo.timeout > Date.now()) {
      return true;
    }
    else {
      post('login', {account: userInfo.userName.trim(), password: userInfo.passWord.trim()}, function (data,success) {
        if(success){
          if (data) {
            let today = new Date()
            data["timeout"]=today.setDate(today.getDate() + 1);//一天过期
            data["password"]=userInfo.passWord.trim();
            setStorage(USERINFO_STORAGE, data);
            return true;
          }
          else{
            return false;
          }
        }
        else{
          alert(TIP_MESSAGE.auto_login_err);
          return false;
        }

      });
    }
  }
  else {
    return false;
  }
}




