
<template>
  <div class="f_btn is-fixed">
    <router-link to="/" class="f_btn_item" active-class="selected" exact>
      <div class="f_item_icon">
        <img src="../../assets/image/home/home_img_1.png" />
      </div>
      <div class="f_item_lable">
        首页
      </div>
    </router-link>
    <router-link to="/category" class="f_btn_item" active-class="selected" exact>
      <div class="f_item_icon">
        <img src="../../assets/image/home/classify_img_1.png" />
      </div>
      <div class="f_item_lable">
        分类
      </div>
    </router-link>
    <router-link to="/cart" class="f_btn_item" active-class="selected" exact>
      <div class="f_item_icon">
        <img src="../../assets/image/home/shopcart_img_1.png" />
      </div>
      <div class="f_item_lable">
        购物车
      </div>
    </router-link>
    <router-link to="/member" class="f_btn_item" active-class="selected" exact>
      <div class="f_item_icon">
        <img src="../../assets/image/home/shopcart_img_1.png" />
      </div>
      <div class="f_item_lable">
        我的
      </div>
    </router-link>
  </div>
</template>

<script type="text/ecmascript-6">
  import {mapGetters} from 'vuex'

export  default{
  data(){
    return {
      barSelected: 0
    }
  },
  computed: {
    ...mapGetters({
      cartStatistics: 'cartStatistics'
    })

  },
  created(){
    var nowRouter = this.$route.path;

    if(nowRouter=='/home'||nowRouter=='/')
    {
        this.barSelected=1;
    }
    else if(nowRouter=='/category'){
        this.barSelected=2;
    }
    else if(nowRouter=='/cart'){
        this.barSelected=3;
    }
    else if(nowRouter=='/member'){
        this.barSelected=4;
    }

  },
  methods:{
  },
  watch:{

  }
}
</script>





