import Vue from 'vue'
import Router from 'vue-router'
import {isLogin} from '../common/js/checkIsLogin'
import Home from '../components/home/index'
import Product from '../components/product/product'
import Login from '../components/login/login'
import Regiter from '../components/login/regiter'
import Member from '../components/member/member'
import Cart from '../components/cart/cart'
import store from '../store'
Vue.use(Router)
var _this=this;
const routers=new Router({
  mode: 'hash',
  routes: [

    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path:'/product/:sku',
      name:'product',
      component:Product
    },
    {
      path:'/login',
      name:'login',
      component:Login
    },
    {
      path:'/regiter',
      name:'regiter',
      component:Regiter
    },
    {
      path:'/member',
      name:'member',
      component:Member,
      meta:{
        requiresAuth:true
      }
    },
    {
      path:'/cart',
      name:'cart',
      component:Cart
    }
  ]
})
routers.beforeEach((to,from,next)=>{
  if(to.meta&&to.meta.requiresAuth===true&&isLogin()==false){
    next({
      path:'/login',
      query:{
        redirce:to.fullPath
      }
    });
  }
  else{
    store.commit('UPDATELOADINGSTATUS',{value:true})//设置loading状态
    next();
  }
});
routers.afterEach(function (to) {
  setTimeout(function () {
    store.commit('UPDATELOADINGSTATUS',{value:false})//设置loading状态
  },1000)

})
export default routers;




