<template>
<div>
  <div v-for="seller in cartStatistics.sellers">
    <div style="width: 100%;height: 30px;border: 1px solid #000">
      <input type="checkbox">
      {{seller}}
      <!--{{cartInfo.find(c=>c.sellerId==seller).sellerName}}-->
    </div>
    <div v-for="cartPro in cartInfo">
      <div v-if="cartPro.sellerId==seller" style="width: 100%;border: 1px solid #eee;height: 150px;padding-top: 25px">
        <div style="float: left;width: 10%;"><input type="checkbox" v-model="cartPro.checked" @click="check_cart(cartPro)"></div>
        <div style="float: left;width: 40%;"><img src="" height="100px" width="100px"></div>
        <div style="float: left;width: 50%;">{{cartPro.productName}}</div>
        <div style="float: left;width: 100%;">{{cartPro.sku}}----{{cartPro.price}}</div>
        <div style="width: 100%;height: 30px">
          <input type="button" value="-" style="float: left; width: 20px" @click="delete_cart(cartPro.sku)">
          <input v-model="cartPro.quantity" style="float: left;width: 50px">
          <input type="button" value="+" style="float: left;width: 20px" @click="add_cart(cartPro)">
        </div>
      </div>
    </div>
  </div>
  <div>数量：{{cartStatistics.count}}</div>
  <div>总价：￥{{cartStatistics.totalMoney.toFixed(2)}}</div>
</div>
</template>

<script type="text/ecmascript-6">
import {mapGetters} from 'vuex'
  export default{
    data(){
      return {
        sellers: []
      }
    },
    computed: {
      ...mapGetters({
        cartInfo: 'getCartInfo',
        cartStatistics: 'cartStatistics'

      })
    },
    created(){

    },
    methods: {
      add_cart(product){
        this.$store.dispatch('addToCart', product)
      },
      delete_cart(sku){
        this.$store.dispatch('deleteToCart', sku);
      },
      check_cart(product){
        this.$store.dispatch('checkToCart',product);
      }
    }
  }



</script>


<style>

</style>



