<template>
  <div>
    <div>
      <input type="text" v-model.userInfo.userName="userInfo.userName"/>
      <input type="password" v-model="userInfo.passWord">
      <input type="button" @click="loginIn" value="登录"  :disabled="isClick">


      <div @click="wechatLogin()" style="width: 100px; height: 50px;">微信登录</div>
      <div @click="wechatShare()">微信分享</div>
    </div>
    <confirm v-model="show" :title="$t('confirm deleting the item')">
      <p style="text-align:center;">{{ $t('Are you sure?') }}</p>
    </confirm>
    <input type="button" value="confrim" @click="showconfrim()">
  </div>
</template>

<script type="text/ecmascript-6">
import {post} from '../../common/js/public-resource'
import {USERINFO_STORAGE,TIP_MESSAGE} from '../../common/public-variable'
import {setStorage} from '../../common/js/storage'
import Confirm from 'vux/src/components/confirm/index.vue'


export default{

  data(){
    return {
      userInfo: {
        userName: "",
        passWord: ""
      },
      isClick: true,
      show:false
    }
  },
  methods: {
    loginIn(){
      var _this = this;
      post('login', {
        account: this.userInfo.userName.trim(),
        password: this.userInfo.passWord.trim()
      }, function (data, success) {
        if (data && success) {
          alert(TIP_MESSAGE.login_success);
          let today = new Date()
          data["timeout"] = today.setDate(today.getDate() + 1);//一天过期
          data["password"] = _this.userInfo.passWord;
          setStorage(USERINFO_STORAGE, data);
          let query = _this.$route.query;
          if (query) {
            _this.$router.go({name: query.redirce})
          }
        } else {
          alert(data);
        }
      });
    },
    enableLoginBtn(){
      console.log(this.userInfo)
    },
    wechatLogin(){
      var scope = "snsapi_userinfo",
        state = "_" + (+new Date());
      Wechat.auth(scope, state, function (response) {
        // you may use response.code to get the access token.
        alert(JSON.stringify(response));
      }, function (reason) {
        alert("Failed: " + reason);
      });
    },
    showconfrim(){

      this.$vux.confirm.show({
        title: 'Title',
        content: 'Content',
        hideOnBlur: true,
        onConfirm () {
          console.log('plugin confirm 3')
        }
      })
    },
    wechatShare(){
      Wechat.share({
        message: {
          title: "Hi, there",
          description: "This is description.",
          thumb: "www/img/thumbnail.png",
          mediaTagName: "TEST-TAG-001",
          messageExt: "这是第三方带的测试字段",
          
          media: "e.g. link"
        },
        scene: Wechat.Scene.TIMELINE   // share to Timeline
      }, function () {
        alert("Success");
      }, function (reason) {
        alert("Failed: " + reason);
      });
    }
  }
    ,
  watch: {
    'userInfo': {
      handler: function (val, oldVal) {
        if (val.userName.trim().length > 0 && val.passWord.trim().length > 0) {
          this.isClick = false;
        }
        else {
          this.isClick = true;
        }
      },
      deep: true
    },
    deep: true
  },
  components:{
    Confirm
  },
  created(){
      console.log(this.$vux)
  }
}
</script>

<style>

</style>



